<template>
<CRow>
    <CCol col="12" class="mb-5">
        <CRow>
            <CCol col="8">
                <h3 class="font-weight-bold text-uppercase mb-1">roles permission Formulir</h3>
                <p class="text-primary text-sm-left mb-0">{{$options.identitas[0].instansi}}</p>
                <p class="text-primary text-sm-left">{{$options.identitas[0].provinsi}}</p>
            </CCol>
            <CCol>
                <div class="text-right mt-5 mr-5">
                    <router-link :to="{ path:'/settings/roles'}" class="btn btn-primary text-white">Halaman Roles</router-link>
                </div>
            </CCol>
        </CRow>
    </CCol>
    <CCol col="12">
        <CRow>
            <CCol sm="4">
                <CInput label="Role" v-model.trim="$v.role.role.$model" :isValid="$v.role.role.$dirty ? !$v.role.role.$error : null" placeholder="Ketik Nama Role" />
            </CCol>
            <CCol sm="3">
                <CSelect :isValid="$v.role.routeDefault.$dirty ? !$v.role.routeDefault.$error : null" :options="optionsSelect" :value.sync="$v.role.routeDefault.$model" :lazy="true" invalidFeedback="asdas" label="Routing Default" placeholder="Ketik Routing Default" />
            </CCol>
            <CCol sm="3">
                <CSwitch class="mr-1 mt-4 pt-1" color="primary" :checked.sync="role.active" v-model="role.active" />
            </CCol>
        </CRow>
    </CCol>
    <CCol col="12">
        <div class="wrapperModul mt-2">
            <div v-for="(i,index) in table.data" :key="index">
                <div class='p-2'>
                    <CRow class="mb-2 font-weight-bold">
                        <CCol>{{index +1}}. {{i.name}}</CCol>
                        <CCol class="ml-2">
                            <input type="checkbox" v-model="i.crud.create" /> &nbsp; Create
                            {{i.crud.create}}
                        </CCol>
                        <CCol>
                            <input type="checkbox" v-model="i.crud.read" /> &nbsp; Read
                        </CCol>
                        <CCol>
                            <input type="checkbox" v-model="i.crud.update" /> &nbsp; Update
                        </CCol>
                        <CCol>
                            <input type="checkbox" v-model="i.crud.delete" /> &nbsp; Delete
                        </CCol>
                    </CRow>
                    <CRow v-for="(c,Cindex) in i.child" :key="Cindex">
                        <CCol class="ml-2">{{Cindex+1}}. {{c.name}}</CCol>
                        <CCol>
                            <input type="checkbox" v-model="c.crud.create" @click="ClickedOnChild(index,Cindex,'create')" /> &nbsp; Create
                            {{c.crud.create}}
                        </CCol>
                        <CCol>
                            <input type="checkbox" v-model="c.crud.read" @click="ClickedOnChild(index,Cindex,'read')" /> &nbsp; Read
                        </CCol>
                        <CCol>
                            <input type="checkbox" v-model="c.crud.update" @click="ClickedOnChild(index,Cindex,'update')" /> &nbsp; Update
                        </CCol>
                        <CCol>
                            <input type="checkbox" v-model="c.crud.delete" @click="ClickedOnChild(index,Cindex,'delete')" /> &nbsp; Delete
                        </CCol>
                    </CRow>
                </div>
                <div class="float">
                    <CButton color="primary" block @click="ToDatabase()" class="text-white">{{type}} Data</CButton>
                </div>
            </div>
        </div>
    </CCol>
</CRow>
</template>

<script>
import identitas from '@/containers/_identitas'
import tableta from '@/views/table/table'
import {
    required
} from 'vuelidate/lib/validators'
const axios = require('axios');
export default {
    identitas,
    components: {
        tableta
    },
    filters: {
        stringToBoolean: function (string) {
            switch (string) {
                case "true":
                case "yes":
                case "1":
                    return true;
                case "false":
                case "no":
                case "0":
                case null:
                    return false;
                default:
                    return Boolean(string);
            }
        }
    },
    data() {
        return {
            table: {
                header: ['Name', 'Type'],
                rows: ['name', 'type'],
                data: [],
                activeItem: null,
                opened: [],
                search: null
            },
            role: {
                role_id: null,
                role: null,
                routeDefault: null,
                active: false
            },
            type: this.$route.params.type === 'edit' ? "update" : "insert",
            toasNot: false
        }
    },
    validations: {
        role: {
            role: {
                required
            },
            routeDefault: {
                required
            },
        }
    },
    created() {
        this.getData()
    },
    computed: {
        optionsSelect() {
            var data = this.table.data.map((header, index) => {
                return {
                    label: header.name,
                    value: header.name,
                    active: header.crud.read
                };
            });
            var filter = data.filter((e) => {
                return e.active.toString().includes("true") || e.active.toString().includes("1");
            });
            return filter;
        }
    },
    methods: {
        getData() {
            axios.post(this.$options.identitas[0].url + '/roles/permission?type=dataById', {
                    id: this.$route.params.id
                })
                .then((e) => {
                    e.data.code === "404" ? this.$router.push({
                        path: '/pages/404'
                    }) : (this.table.data = e.data.data.permission, this.role = e.data.data.role)

                })
                .catch(function (error) {
                    // handle error
                    console.log(error);
                })
        },
        ToDatabase() {
            this.$v.$touch()
            if (!this.$v.$invalid) {
                axios.post(this.$options.identitas[0].url + '/roles/permission?type=' + this.type, {
                        data: this.table.data,
                        role: this.role
                    })
                    .then((e) => {
                        this.$notify({
                            group: 'foo',
                            title: 'Information',
                            text: this.$route.params.type === 'edit' ? "Data Berhasil Di Ubah" : "Data Berhasil Di Tambahkan",
                        });

                        this.$router.push({
                            path: '/settings/roles'
                        })
                    })
                    .catch(function (error) {
                        // handle error
                        console.log(error);
                    })
            }
        },
        ClickedOnChild(parent, child, type) {
            this.table.data[parent].crud[type] = true
            this.table.data[parent].crud['read'] = true
            this.table.data[parent].child[child].crud['read'] = true
        }
    }

}
</script>

<style>
.wrapperModul {
    border: 1px solid #dfdfdf;
    border-radius: 5px;

}

.float {
    position: fixed;
    width: 120px;
    bottom: 80px;
    right: 40px;
    color: #FFF;
    text-align: center;
    box-shadow: 2px 2px 3px #999;
}
</style>
